import certicate from '@/views/ucenter/resume/certificate'
 
export default {
    components: {
        certicate
    },
    watch: {
        byShow (val) {
            if (val == true) {
               this.$refs.certicate.certicateshow = true
            }
        }
    },
    data () {
        return {
            byShow: false,
            lisencedata: {}
        }
    },
    methods: {
        postchange (val) {
            this.byShow = false
            this.lisencedata = val
            this.chooselisence()
        },
        chooselisence () {
            this.$emit("chooselisence", this.lisencedata)
        }      
    }

}



